#swiper-box {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
#swiper-box li {
  width: 224px;
  padding-bottom: 70px;
}
#swiper-box li p {
  padding-top: 10px;
  font-size: 14px;
}
#swiper-box li span {
  font-size: 12px;
  color: #666;
}
#swiper-box li .img-box {
  width: 224px;
  height: 224px;
  background-color: pink;
  position: relative;
  overflow: hidden;
}
.paly-icon {
  -webkit-transition: all 0.8s;
  transition: all 0.8s;
  opacity: 0;
  border-radius: 50%;
  background-color: #fff;
  position: absolute;
}
#swiper-box .img-box .paly-icon {
  width: 40px;
  height: 40px;
  left: 50%;
  top: 50%;
  margin-left: -20px;
  margin-top: -20px;
}
.img-box:hover .paly-icon {
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  opacity: 1;
}
.img-box:hover img {
  -webkit-transform: scale(1.05);
          transform: scale(1.05)
}
.paly-icon:after {
  content: '';
  position: absolute;
  height: 0;
  width: 0;
}
#swiper-box .img-box .paly-icon:after {
  left: 16px;
  top: 12px;
  border-bottom: 8px solid transparent;
  border-left: 14px solid #666;
  border-top: 8px solid transparent;
}
#swiper-box li .img-box img {
  width: 100%;
  height: 100%;
  display: block;
  -webkit-transition: all 1s;
  transition: all 1s;
}
.swiper-container-horizontal>.swiper-pagination-bullets, 
.swiper-pagination-custom, .swiper-pagination-fraction {
  bottom: 25px;
}
.page-home #songlists {
  display: -webkit-box;
